.component{
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    margin-bottom: 20px;
}
.title{
    font-size: 18px;
    font-weight: 600;
    padding-bottom: 10px;
    margin-bottom: 30px;
    position: relative;
}
.title::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 40px;
    height: 1px;
    background-color: #DC4734;
}