body {
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
p {
    padding: 0;
    margin: 0;
}
a{
    text-decoration: none;
}
li {
    list-style: none;
}
ul{
    padding: 0;
    margin: 0;
}
input:focus::placeholder{
    color: transparent;
}
button{
    cursor: pointer;
}
input,
textarea,
button{
    outline: none;
}