/* Dropdown menu*/
.homeDropdown{
    display: none;
    position: absolute;
    right: 20px;
    top: 60px;
    width: 200px;
    background-color: #ffffff;
    border-radius: 3px;
    box-shadow: 0px 1px 15px 0px rgba(27, 27, 27, 0.3);
    padding: 20px 20px 0 20px;
    color: #444444;
    z-index: 20;
}
.homeDropdownItem{
    padding: 15px 0;
    border-bottom: 1px solid #ccc;
}
.homeDropdownItem:nth-child(1){
    padding: 0 0 15px 0;
}
.homeDropdownItem:nth-child(3){
    border: none;
}
.homeDropdown ul li{
 padding-bottom: 7px;
}
.menuHeaderName{
    font-size: 18px;
}
.menuHeaderEmailLink{
    color: #444444;;
    transition: color 0.15s linear;
}
.menuHeaderEmailLink:hover{
    color: var(--main-bg-color);
}
.menuLink{
    color: #444444;;
    transition: color 0.15s linear;
}
.menuItem:hover{
    color: var(--main-bg-color);
}