.postForm{
    position: relative;
    /* display: flex;
    align-items: center;*/
    height: 100%;
    width: 88%;
}
.postTextarea{
    width: 99%;
    /* height: 88%; */
    height: 31px;
    background-color: #F1F1F1;
    border: none;
    resize: none;
    box-shadow: inset 0px 1px 15px 0px rgba(119, 66, 66, 0.2);
    border-top-left-radius: 25px; 
    border-bottom-left-radius: 25px; 
    border-top-right-radius: 25px; 
    border-bottom-right-radius: 25px; 
    outline: none;
    padding: 13px 0 0 11px;
}
.postTextarea:focus::placeholder{
    color: transparent;
}
.postBtn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-52%);
    border: none;
    padding: 10px 20px;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    background-color: #DC4734;;
    border-top-left-radius: 25px; 
    border-bottom-left-radius: 25px; 
    border-top-right-radius: 25px; 
    border-bottom-right-radius: 25px;
    transition: all 0.15s linear;
}
.postBtn:hover{
    background-color: #333;
}