.loginPageWrap{
    position: relative;
}
.loginWrapper{
    width: 100%;
    height: auto;
    background-image: url(./../../images/auth.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    --main-bg-color: #DC4734
}
.login{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}
.container{
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 15px;
}
.loginHeader{
    height: 70px;
    display: flex;

}
.loginHeaderlogo{
    padding: 0 0 0 20px;
    flex: 1 0 0;
    display: flex;
    align-items: center;
}
.headerImg{
    margin-right: 30px;
}
.loginHeaderForm{
    width: 650px;
    background-color: #DC4734;
}
.headerForm{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.headerForm input{
    width: 215px;
    font-size: 16px;
    border: none;
    box-shadow: none;
    outline: none;
    color: #fff;
    background-color: var(--main-bg-color);
    padding: 8px 5px;

    border-bottom: 1px solid #ffffff;
    border-top: none;
    border-right: none;
    border-left: none;

}
.headerForm input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #DC4734 !important; /* Цвет фона */
    -webkit-text-fill-color: #fff !important; /* цвет текста */
    color: #fff !important; /* цвет текста */
}
.headerForm input::placeholder{
    color: #fff;
}
.headerForm button{
    font-size: 16px;
    border: none;
    box-shadow: none;
    outline: none;
    color: var(--main-bg-color);
    background-color: #fff;
    padding: 8px 20px;
    cursor: pointer;
    transition: all 0.3s linear;
}
.headerForm button:hover{
    color: #DC4734;
    background-color: #333;
}
.authFormBlock{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 650px;
    /* height: 100vh; */
    /* height: 700px; */
    background-color: #fff;
    padding: 35px 0 30px;
}
.authTitle{
    font-size: 34px;
    font-weight: bold;
    color: var(--main-bg-color);
    margin-bottom: 20px;
}
.authFormWrap{
    width: 85%;
    background-color: #fff;
    box-shadow: 0px 3px 20px 0px rgba(0, 0, 0, 0.25);
}
.authFormWrapTitle{
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: var(--main-bg-color);
    font-size: 20px;
    font-weight: bold;
}
.authFormOuter{
    width: 100%;
    margin: 0 auto;
    background-color: #fff;
    padding: 30px 0 50px;
}
.authForm{
    /* display: flex;
    flex-direction: column;
    align-items: center; */
    /* width: 550px; */
    width: 80%;
    margin: 0 auto;
    /* padding: 15px; */
}
.inputContainer{
    width: 100%;
    /* width: 75%; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.authInput{
    border-bottom: 1px solid #999999;
    border-top: none;
    border-right: none;
    border-left: none;
    outline: none;
    box-shadow: none;
    background-color: #fff;
    color: #636363;
    font-size: 14px;
    padding: 12px 8px;
    margin-bottom: 15px;
}
.authInput:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
    -webkit-text-fill-color: #636363 !important; /* цвет текста */
    color: #999 !important; /* цвет текста */
}
.authLargeInput{
    width: 96.5%;
}
.authSmallInput{
    width: 42%;    
}
.authFormbtn{
    border: none;
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    padding: 20px 0;
    margin: 40px 0 0 0;
    color: #ffffff;
    background-color: var(--main-bg-color);
    cursor: pointer;
    transition: all 0.3s linear;
}
.authFormbtn:hover{
    color: #DC4734;
    background-color: #333;
}
.terms{
    text-align: center;
    padding-top: 25px;
}
.termsLink{
    text-decoration: none;
    color: var(--main-bg-color);
}