.post{
    margin-top: 20px;
    padding: 30px 25px;
    background-color: #ffffff;
    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);    
}
.postHeader{
    display: flex;
    align-items: center;
}
.postCreatorPhoto{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.postInfo{
    margin-left: 25px;
}
.postText{
    margin-top: 20px;
    text-align: justify;
}
.postPhotos{
    margin-top: 25px;
}
.postPhotosItem{
    width: 100%;
    height: auto;
    border-radius: 5px;
}