@keyframes preloaderItems {
    0% {
      opacity: 1
    }

    100% {
      opacity: 0
    }
}

.preloaderItems div {
    left: 50%;
    top: 20%;
    position: absolute;
    animation: preloaderItems linear 1s infinite;
    background: var(--main-color);
    width: 19px;
    height: 36px;
    border-radius: 10.5px / 21px;
    transform-origin: 10.5px 79px;
    --main-color: #DC4734;
  }

.preloaderItems div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -0.9166666666666666s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -0.8333333333333334s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.75s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.6666666666666666s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.5833333333333334s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.5s;
    background: var(--main-color);
  }

  .preloaderItems div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.4166666666666667s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.3333333333333333s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.25s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.16666666666666666s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.08333333333333333s;
    background: var(--main-color);
  }

.preloaderItems div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    background: var(--main-color);
  }

.preloader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    overflow: hidden;
    background: #F5F7F6;
    opacity: 0.65;
  }

.preloaderItems {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
    /* see note above */
  }

.preloaderItems div {
    box-sizing: content-box;
  }