.headerWrap{
    background-color: #fff;
    width: 100%;
    position: fixed;
    z-index: 15;
    box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.3);
}
.header{
    position: relative;
    display: grid;
    grid-template-rows: 4rem;
    grid-template-columns: 40% 20% 40%;
    width: 100%;
    background-color: #ffffff;
    --main-bg-color: #DC4734
}
.headerBtnBlock{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 350px;
    padding: 0 0 0 30px;
}
.headerBtnItem{
    font-size: 18px;
    font-weight: 600;
    color: #000000;
    text-decoration: none;
    transition: color 0.15s linear;
}
.headerBtnItem:hover{
    color: var(--main-bg-color);
}
.headerLogo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.searchBlock{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 20px 0 0;
}
.profSmallPhoto{
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.loginBlock{
    margin-right: 30px;
}
.authBtn{
    border: none;
    background-color: #DC4734;
    color: #ffffff;
    padding: 4px 15px;
    border-radius: 4px;
}
.authBtn:hover{
    background-color: #c03e2d;
}