.usersItem{
    height: 60px;
    padding: 20px;
    display: flex;
    align-items: center;
    background-color: #fff;
    box-shadow: 0px 1px 5px 0px rgba(51, 51, 51, 0.2);
    transition: all 0.15s linear;
}
.usersItem:hover{
    background-color: #DC4734;
}
.usersItem:hover .userName{
    color: #ffffff;
}
.usersImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.usersContent{
    margin-left: 20px;

}
.usersName{
    font-weight: 400;
    color: #333333;
}
.usersFollowBtn{
    margin-top: 5px;
    padding: 6px 0;
    width: 87px;
    background-color: rgb(53, 53, 53);
    color: #ffffff;
    border: none;
    border-radius: 3px;
    box-shadow:  8px 7px 8px -5px rgba(0,0,0,0.5);
    transition: all 0.15s linear;
}
.usersFollowBtn:hover{
    background-color: rgb(36, 36, 36);
}