.homeNavWrap{
    height: 4rem;    
    background-color: #fff;
}
.homeNav{
    display: flex;
    height: 4rem;
}
.homeNavPhoto{
    transform: translateY(-40.5%);
    width: 270px;
    height: 270px;
    /* position: relative; */
    background-color: #ffffff;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.homeNavPhoto img{
    width: 250px;
    height: 250px;
    /* position: absolute;
    top: 10px;
    left: 10px; */
}
.nav{
    flex: 1 0 auto;
}
.navList{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}
.space{
    flex: 0 1 7rem;
}
.homeNavItem{
    color: #272727;
    font-size: 16px;
    font-weight: 600;
    transition: color 0.15s linear;
}
.homeNavItem:hover{
    color: #DC4734;
}
.homeNavItem a.active{
    color: #DC4734;
}
