.btnType_1 {
    text-decoration: none;
    outline: none;
    display: inline-block;
    width: 100px;
    height: 35px;
    line-height: 35px;
    border-radius: 35px;
    /* margin: 10px 20px; */
    /* font-family: 'Montserrat', sans-serif; */
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: white;
    box-shadow: 0 8px 15px rgba(0,0,0,.1);
    transition: .3s;
    cursor: pointer;
}
.btnType_1:hover {
    background-color: #DC4734;
    box-shadow: 0 10px 15px #8b2c20;
    /* background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4); */
    color: white;
    transform: translateY(-5px);
}