.usersListWrap{
    position: relative;
    min-height: 200px;
}
.usersList{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    background-color: #F1F1F1;
    column-gap: 20px;
    row-gap: 20px;
}
.userListNav{
    margin-top: 40px;
}
.userListNav.disabled{
    visibility: hidden;
    margin-top: 40px;
}
.userListNav ul{
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
}
.navButtonsItem{
    margin-left: 20px;
    padding: 10px;
    border: 1px solid #FF5E3A;
    /* background-color: #FF5E3A; */
    color: #b93422;
    line-height: 1;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.15s linear;
}
.navButtonsItem:hover{
    color: #ffffff;
    background-color: #FF5E3A;
}
.navButtonsItem.active{
    color: #ffffff;
    background-color: #FF5E3A;
    box-shadow: 0px 0px 12px 2px #E85535;
}
.navButtonsItem:nth-child(1){
    margin: 0;
}