.photoTitle{
    color: #DC4734;
    font-size: 14px;
}
.inputBlock{
    width: 80%;
}
.photoInput{
    width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}
.photoLabel{
    width: 100%;
}
.photoButton{
    width: 80%;
}
.photoLabel,
.photoButton {
    font-size: 18px;
    padding: 15px 0;
    border-radius: 4px;
    text-align: center;
    margin: 25px;
    cursor: pointer;
    display: block;
    transition: all 0.18s ease-in-out;
    border: 1px solid #DC4734;
    background: linear-gradient(to top right, #DC4734, #fd6543 20%, rgba(255, 255, 255, 0) 80%, rgba(255, 255, 255, 0)) top right/500% 500%;
    color: #DC4734;
}

.photoLabel:hover,
.photoButton:hover {
    color: white;
    background-position: bottom left;
}